
@mixin tablet {
    @media only screen and  (min-width: 601px){
        @content;
    }
}
@mixin desktop {
    @media only screen and  (min-width: 1024px){
        @content;
    }
}
@mixin xSmall{
    @media only screen and (max-width: 350px) {
        @content;
    }
}
@mixin handHeld {
    @media only screen and (max-width:1023) {
        @content;
    }
    
}

